.footer {
    background-color: black;
    color: #fff;
    position: absolute;
    width: 100%;
    height: 35px;
    bottom: 0;
}

.div-only-mobile {
    display: none;
}

.div-no-mobile {}

@media screen and (max-width: 849px) {
    .div-no-mobile {
        display: none;
    }
    .div-only-mobile {
        display: block;
    }
}

.line {
    overflow: hidden;
    position: relative;
}

.sm-icon {
    padding: 0.5em;
}

.name-of-blog {
    padding-left: 0.4em;
    /* LEFT WITH INTENTION OF FUTURE IDEAS ABOUT DESIGN  */
}

.line::after {
    content: '';
    width: 100%;
    box-sizing: border-box;
    border-top: 1px solid gray;
    position: absolute;
    bottom: 0.5em;
    margin-left: 0.5em;
}

.no-select {
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.btn {
    border-radius: 4px;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #282c34;
    margin: 0 auto;
    background-color: #61afef;
    font-size: 17px;
    line-height: 30.5px;
    font-weight: bolder;
    font-family: Tahoma, Geneva, sans-serif;
}

.toggleButtonDar {
    background-color: #282c34 !important;
    color: #e5c07b !important;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.toggleButtonLig {
    background-color: #e5c07b !important;
    color: #282c34 !important;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

body {
    min-height: 100vh;
    margin: 0;
    position: relative;
    background-color: #282c34;
    position: relative;
    color: #abb2bf;
    font-family: Tahoma, Geneva, sans-serif;
    font-size: 17px;
    line-height: 30.5px;
    margin: 0 auto;
}

body::after {
    content: '';
    display: block;
    height: 50px;
    /* Set same as footer's height */
}

.Non-footer-stuff {
    background-color: #282c34;
    color: #abb2bf;
    font-family: Tahoma, Geneva, sans-serif;
    font-size: 17px;
    line-height: 30.5px;
    margin: 0 auto;
    max-width: min(100%, 52em);
    padding-bottom: 1em;
    padding-top: 1em;
}

::-webkit-scrollbar {
    display: none;
}

pre {
    color: #e5c07b;
    font-family: Tahoma, Geneva, sans-serif;
    font-size: 17px;
    line-height: 30.5px;
    word-wrap: break-word;
    white-space: pre;
    white-space: -moz-pre-wrap;
    white-space: pre-wrap;
    white-space: pre\9;
}

blockquote {
    color: #e5c07b;
}

a {
    color: #c678dd;
    text-decoration: none;
}


/* ALTERNATIVE TO TRADITIONAL hr */

.horizontal-rule {
    border-top: 0.1em solid;
}

a:hover {
    color: #61afef;
}

h1 {
    color: #e06c75;
    margin-bottom: 0px;
}

h2 {
    color: #61afef;
    margin-bottom: 0px;
}

h3,
h4,
h5,
h6 {
    color: #56b6c2;
    margin-bottom: 0px;
}

img {
  border-radius: 5%;
}

p {
    /* padding: 0px; */
    /* margin: 0px; */
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
:root {
	--bg-color: #efdfae;
	--text-color: #282c34;
	--pre-color: #665c54;
	--h2-color: #377375;
	--h3-color: #719586;
	--lk-color: #a04b73;
}

[data-theme="dark"] {
	--bg-color: #282c34;
	--text-color: #abb2bf;
	--pre-color: #e5c07b;
	--h2-color: #61afef;
	--h3-color: #56b6c2;
	--lk-color: #c678dd;
}

.Main {
	background-color: #efdfae;
	background-color: var(--bg-color);
	color: #282c34;
	color: var(--text-color);
	webkit-transition: background 1s ease-out;
	transition: background 1s ease-out;
	border-radius: 0.2em;
	padding: 2em 0 0 0;
}

.Main hr {
	border: 1px solid #282c34;
	border: 1px solid var(--text-color);
}

.Main a {
	color: #a04b73;
	color: var(--lk-color);
}

.Main pre {
	color: #665c54;
	color: var(--pre-color);
}

.Main h2 {
	color: #377375;
	color: var(--h2-color);
}

.Main h3, h4, h5, h6 {
	color: #719586;
	color: var(--h3-color);
}
